// import logo from './logo.svg';
// import './App.css';
import Column from './Column';
import { DragDropContext } from 'react-beautiful-dnd';
import { useEffect, useState } from 'react';
import initialData from './initial-data';
import Layout from './Layout';
import uuid from 'react-uuid';

const Container = ({ children }) => {
  return (
    <div className='flex '>
      {children}
    </div>
  )
}

function App() {

  const storedTask = JSON.parse(localStorage.getItem('taskState')) || initialData;
  const [state, setState] = useState(storedTask);

  const [inEditMode, setInEditMode] = useState(false);
  const [inAddMode, setInAddMode] = useState(false);

  const [currentTaskId, setCurrentTaskId] = useState('');
  const [currentTaskCat, setCurrentTaskCat] = useState('');
  
  const [options, setOptions] = useState([]);

  useEffect(() => {
    localStorage.setItem('taskState', JSON.stringify(state));
  }, [state]);

  useEffect(() => {
    setOptions(Object.values(state.tasks).map(task => {
      task.label = task.title;
      return task;
    }));
  }, [state]);

  const editTask = (task) => {
    setInEditMode(true);
    setCurrentTaskId(task.id);

    for (const key in state.columns) {
      if (state.columns[key].taskIds.includes(task.id)) {
        setCurrentTaskCat(key);
        break;
      }
    }

  }

  const onEdit = (editedTask, selectedColumn) => {
    const newState = {
      ...state,
      tasks: {
        ...state.tasks,
        [editedTask.id]: editedTask
      }
    };

    // check whether the category of the edited task is changed
    let prevCat = '';
    for (const key in state.columns) {
      if (state.columns[key].taskIds.includes(editedTask.id)) {
        prevCat = key;
        break;
      }
    }

    setCurrentTaskCat(selectedColumn);

    if (selectedColumn && prevCat !== selectedColumn) {
      // TODO (1) - remove task from prev column
      const updatedTaskIds1 = state.columns[prevCat].taskIds.filter(taskId => taskId !== editedTask.id);

      // TODO (2) - add task from next column
      const updatedTaskIds2 = [...state.columns[selectedColumn].taskIds, editedTask.id];

      // TODO (3) - merge task together
      const finalState = {
        ...newState,
        columns: {
          ...newState.columns,
          [prevCat]: {
            ...newState.columns[prevCat],
            taskIds: updatedTaskIds1
          },
          [selectedColumn]: {
            ...newState.columns[selectedColumn],
            taskIds: updatedTaskIds2
          }
        }
      }

      setState(finalState);
      return;
    }

    setState(newState);
  }

  const sidePanelOff = () => {
    setInEditMode(false);
    setCurrentTaskId('');
    setCurrentTaskCat('');
  }

  const addPanelOn = () => {
    setInEditMode(true);
    setInAddMode(true);

    const newTaskId = uuid();
    const newState = {
      ...state,
      tasks: {
        ...state.tasks,
        [newTaskId]: { id: newTaskId, title: '', description: '' },
      },
      columns: {
        ...state.columns,
        'column-1': {
          ...state.columns['column-1'],
          taskIds: [...state.columns['column-1'].taskIds, newTaskId]
        }
      }
    };

    setCurrentTaskId(newTaskId);
    setCurrentTaskCat('column-1');
    setState(newState);
  }

  const onDelete = (taskId) => {
    const { [taskId]: _, ...rest } = state.tasks;


    for (const key in state.columns) {
      if (state.columns[key].taskIds.includes(taskId)) {

        const newState = {
          ...state,
          tasks: rest,
          columns: {
            ...state.columns,
            [key]: {
              ...state.columns[key],
              taskIds: state.columns[key].taskIds.filter(id => id !== taskId)
            }
          }
        }

        setState(newState);
        setInAddMode(false);
        setInEditMode(false);
        break;
      }
    }
  }

  const onDragEnd = (result) => {
    const { destination, source, draggableId } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    const start = state.columns[source.droppableId];
    const finish = state.columns[destination.droppableId];

    if (start === finish) {
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...start,
        taskIds: newTaskIds,
      };

      const newState = {
        ...state,
        columns: {
          ...state.columns,
          [newColumn.id]: newColumn,
        },
      }
      setState(newState);
      return;
    }

    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = {
      ...start,
      taskIds: startTaskIds
    };

    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finish,
      taskIds: finishTaskIds,
    };

    const newState = {
      ...state,
      columns: {
        ...state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish
      }
    };
    setState(newState);
  }

  return (
    <Layout
      inEditMode={inEditMode}
      currentTask={state.tasks[currentTaskId]}
      currentTaskCat={currentTaskCat}
      onEdit={onEdit}
      sidePanelOff={sidePanelOff}
      addPanelOn={addPanelOn}
      onDelete={onDelete}
      options={options}
      editTask={editTask}
    >
      <DragDropContext
        onDragEnd={onDragEnd}
      >
        <Container>
          {state.columnOrder.map(columnId => {
            const column = state.columns[columnId];
            const tasks = column.taskIds.map(taskId => state.tasks[taskId])

            return <Column key={column.id} column={column} tasks={tasks} editTask={editTask} currentTaskId={currentTaskId} />;
          })}
        </Container>

      </DragDropContext>
    </Layout>
  )
    ;
}

export default App;
