import React from 'react'
import Task from './Task'
import { Droppable } from 'react-beautiful-dnd'

const Container = ({ children }) => {
  return (<div
    className='m-2 w-1/3 border-solid border-[#202020] border-2 rounded-md flex flex-col'
  >
    {children}
  </div>)
}

const Title = ({ children }) => {
  return <h3 className='p-2 font-bold'>{children}</h3>
}

const TaskList = ({ children, provided, innerRef, isDraggingOver }) => {
  return <div className={`p-2 ${isDraggingOver ? 'bg-blue-300' : 'bg-white'} transition-colors duration-200 ease-in-out flex-grow min-h-[200px]`} {...provided.droppableProps} ref={innerRef}>
    {children}
  </div>
}

const Column = ({ column, tasks, editTask, currentTaskId }) => {
  return (
    <Container>
      <Title>{column.title}</Title>
      <Droppable droppableId={column.id}>
        {(provided, snapshot) => (
          <TaskList
            innerRef={provided.innerRef}
            provided={provided}
            isDraggingOver={snapshot.isDraggingOver}
          >
            {tasks.map((task, index) => <Task key={task.id} task={task} index={index} editTask={() => editTask(task)} currentTaskId={currentTaskId}></Task>)}
            {provided.placeholder}
          </TaskList>
        )}
      </Droppable>
    </Container>
  );
}

export default Column