import { Autocomplete, TextField } from '@mui/material';
import React, { useEffect, useState } from 'react'

const ComboBox = ({ options, editTask, sidePanelOff }) => {
    return (
        <Autocomplete
            disablePortal
            id="combo-box-demo"
            options={options}
            sx={{ width: 300 }}
            renderInput={(params) => <TextField {...params} label="Search Task" />}
            onChange={(e, value) => {
                if (value) {
                    editTask(value)
                } else {
                    sidePanelOff()
                }
            }
            }
        />
    );
}

const CardForm = ({ currentTask, onEdit, sidePanelOff, onDelete, currentTaskCat }) => {

    const onChange = (e) => {
        onEdit(currentTask, e.target.value);
    }
    return (
        <form className="w-full max-w-lg">
            <div className="w-full flex flex-wrap mb-6">
                <h1 className="w-6/7 text-xl font-medium">Edit Task</h1>
            </div>

            <div className="flex flex-wrap -mx-3 mb-6">
                <div className="w-full px-3 mb-6">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-first-name">
                        Task Title
                    </label>
                    <input className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-last-name" type="text" placeholder="Enter task title here" value={currentTask?.title} onChange={(e) => {
                        const newTask = {
                            ...currentTask,
                            title: e.target.value
                        }
                        onEdit(newTask);
                    }} />
                    {/* <input className="appearance-none block w-full bg-gray-200 text-gray-700 border border-red-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" id="grid-first-name" type="text" placeholder="Jane" /> */}
                    {/* <p className="text-red-500 text-xs italic">Please fill out this field.</p> */}
                </div>
                <div className="w-full px-3">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-last-name">
                        Task Description
                    </label>
                    <textarea rows={4} className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-last-name" type="text" placeholder="Enter task description here" value={currentTask?.description} onChange={(e) => {
                        const newTask = {
                            ...currentTask,
                            description: e.target.value
                        }
                        onEdit(newTask);
                    }} />
                </div>
            </div>
            <div className="flex flex-wrap -mx-3 mb-2">
                <div className="w-full px-3 mb-6 md:mb-0">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
                        Task State
                    </label>
                    <div className="relative">
                        <select className="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" value={currentTaskCat} onChange={onChange}>
                            <option value="column-1">To Do</option>
                            <option value="column-2">In Progress</option>
                            <option value="column-3">Archived</option>
                        </select>
                        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                            <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
                        </div>
                    </div>
                </div>
            </div>
            <div className="flex flex-wrap -mx-3 mt-3 mb-2 content-center justify-end">
                <div className="w-1/2 px-3 mb-6 md:mb-0 flex">
                    <button class="flex-shrink-0 bg-red-500 hover:bg-red-700 border-red-500 hover:border-red-700 text-sm border-4 text-white py-1 px-2 rounded" type="button" onClick={() => {
                        onDelete(currentTask.id)
                    }}>
                        Delete
                    </button>
                    <button class="flex-shrink-0 bg-green-500 hover:bg-green-700 border-green-500 hover:border-green-700 text-sm border-4 text-white py-1 px-2 rounded ml-3" type="button" onClick={() => {
                        sidePanelOff()
                    }}>
                        Confirm
                    </button>
                </div>
            </div>
        </form>
    );
}

const Layout = ({ children, inEditMode, currentTask, setTask, onEdit, sidePanelOff, addPanelOn, onDelete, currentTaskCat, options, editTask }) => {

    return (
        <div className="grid grid-cols-12 gap-4 w-full h-screen bg-[#F1F1F1]">
            <div id="navbar" className="col-span-12 h-[3.75rem] bg-[#202020] px-4 flex items-center">
                <h1 className="text-2xl font-medium text-[#F1F1F1]">Task Management Dashboard</h1>
            </div>
            <div
                id="content"
                className="col-span-8 bg-[#F1F1F1] h-[calc(100vh-3.75rem)] p-4"
            >
                <div className="w-full flex justify-end">
                    <ComboBox className="mb-3" options={options} editTask={editTask} sidePanelOff={sidePanelOff} />
                </div>
                {children}
            </div>
            <aside id="aside" className='col-span-4 bg-[#7E909A] p-4'>
                {inEditMode ? (
                    <CardForm
                        currentTask={currentTask}
                        setTask={setTask}
                        onEdit={onEdit}
                        sidePanelOff={sidePanelOff}
                        onDelete={onDelete}
                        currentTaskCat={currentTaskCat}
                    >

                    </CardForm>) : (<div className="flex flex-wrap -mx-3 mt-3 mb-2 content-center justify-end">
                        <div className="w-full ml-3 px-2 mb-6 md:mb-0">
                            <span className="text-[#F1F1F1] font-medium text-md ">Do you want add a new task?</span>
                            <button className="ml-3 flex-shrink-0 bg-teal-500 hover:bg-teal-700 border-teal-500 hover:border-teal-700 text-sm border-4 text-white py-1 px-2 rounded" type="button" onClick={addPanelOn}>
                                Add a task
                            </button>
                        </div>
                    </div>)}

            </aside>
        </div>
    )
}

export default Layout;
