import { Draggable } from "react-beautiful-dnd";

const Container = ({ children, provided, innerRef, isDragging, task, editTask, currentTaskId }) => {
    return (<div
        className={`p-2 mb-2 border-solid border border-gray-400 rounded-sm ${isDragging ? 'bg-green-300' : 'bg-white'} ${task.id === currentTaskId ? 'bg-yellow-300' : ''} flex`}
        {...provided.draggableProps}
        {...provided.dragHandleProps}
        ref={innerRef}
    >
        <div className="w-5/6">{children}</div>
        <div className="w-1/6">
            <span onClick={() => {
                editTask();
            }
            }>✏️</span>
        </div>
    </div>)
}

const Task = ({ task, index, editTask, currentTaskId }) => {
    return (<Draggable draggableId={task.id} index={index}>
        {(provided, snapshot) => (
            <Container
                provided={provided}
                innerRef={provided.innerRef}
                isDragging={snapshot.isDragging}
                task={task}
                editTask={editTask}
                currentTaskId={currentTaskId}
            >
                {task.title}
            </Container>
        )}
    </Draggable>
    );
}

export default Task